.product__rating{
    display:flex
}


.product{
    display: flex;
    flex-direction: column;
    max-height: 100px;
    max-height: 400px;
    background-color:WHITE;
    margin: 10px;
   z-index: 1; 
   padding: 20px;
   width: 100%;
   align-items: center;
   justify-content: flex-end;
}


.product > img {
    width: 100%;
    max-width: 200px;
    object-fit: contain;
    margin-bottom: 20px;
}

.product__price{
    margin-bottom: 5px;
}

.product__info{
    height: 100px;
    margin-top: 15px;
}

.product > button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734  #9c7e31  #846a29;

}